import React, { Component } from 'react';
import CreateTodoModal from './CreateTodoModal';
import ListView from './ListView';
import TableView from './TableView';

export default class AllViewControler extends Component {
  state = {
    modalOpen: false,
    title: '',
    desc: '', 
    id: null
  }
  toggleModal = () => this.setState({ modalOpen: !this.state.modalOpen }) 

  getTodoInfo = (title, desc, id) => {
    this.toggleModal()
    this.setState({title,desc,id})
  }

  updateTodo = (getUpdateInfo) => {
    this.props.updateTodo(getUpdateInfo, this.state.id)
  }

  render() { 
		return (
			<>
				{this.props.viewChange === 'listview' ? (
          <ListView
            toggleModal={this.toggleModal}
            getTodoInfo={this.getTodoInfo}
						toggleComplete={this.props.toggleComplete}
						toggleSelect={this.props.toggleSelect}
						toggleMarkAll={this.props.toggleMarkAll}
						deleteTodo={this.props.deleteTodo}
						markAll={this.props.markAll}
            todos={this.props.todos}
					/>
				) : (
            <TableView              
            toggleModal={this.toggleModal}
            getTodoInfo={this.getTodoInfo}              
						toggleComplete={this.props.toggleComplete}
						toggleSelect={this.props.toggleSelect}
						toggleMarkAll={this.props.toggleMarkAll}
						deleteTodo={this.props.deleteTodo}
						markAll={this.props.markAll}
						todos={this.props.todos}
					/>
          )}
        {this.state.modalOpen && <CreateTodoModal
          btnValue="Update"
          toggleModal={this.toggleModal}
          todoName={this.state.title}
          todoDesc={this.state.desc}
          submit={this.updateTodo}
        />
        } 
			</>
		);
	}
}
