import React from 'react' 

export default function TableView(props) {
  return ( 
    <table className="table text-light mt-4">
      <thead >
        <tr className="text-warning">
          <th className="text-left pl-5" scope="col">
            <input onChange={props.toggleMarkAll} type="checkbox" className="form-check-input" id="markAll" checked={props.markAll} />
            <label className="m-0" htmlFor="markAll">Mark All</label> 
          </th>
          <th scope="col">Tado name</th>
          <th scope="col">Date</th>
          <th scope="col">Action</th>
        </tr>
      </thead>
      <tbody className="bg-secondary">
        {props.todos.map(todo => (          
        <tr key={todo.id}> 
          <td className="text-left pl-5"> <input onChange={()=>props.toggleSelect(todo.id)} checked={todo.isSelect} type="checkbox" className="form-check-input bg-light" /> </td>
          <td>{todo.todoName}</td>
          <td>{todo.date.toDateString()}</td>
            <td>
              <button onClick={() => props.toggleComplete(todo.id)} className={`btn btn-sm ${todo.isComplete ? 'btn-success' : 'btn-warning'}`}>{todo.isComplete ? 'Complete' : 'Running'}</button>
              <button onClick={()=>props.deleteTodo(todo.id)} className="btn btn-danger btn-sm mx-2">Delete</button>
					   	<button onClick={()=>props.getTodoInfo(todo.todoName,todo.todoDesc,todo.id)} className="btn btn-info btn-sm">Edit</button>
            </td>
        </tr> 
          ))}
      </tbody>
    </table> 
    
  )
}