import React from 'react'; 
export default function ListView(props) {
	return (
		<ul className="list-group mt-4">
			<label htmlFor="markAll" className="btn btn-primary btn-sm text-justify pl-4">
				<input onChange={props.toggleMarkAll} id="markAll" type="checkbox" checked={props.markAll} /> Mark All
			</label>
			{props.todos.map((todo) => (
				<li key={todo.id} className="list-group-item bg-secondary mb-1 py-1 text-white d-flex align-items-center">
					<div className="form-check pr-4 d-flex align-items-center">
						<input onChange={()=>props.toggleSelect(todo.id)} checked={todo.isSelect} type="checkbox" className="form-check-input d-block" />
					</div>
					<div className="d-flex flex-column text-justify">
						<span>{todo.todoName}</span>
						<small>{todo.date.toDateString()}</small>
					</div>
					<div className="ml-auto">
						<button onClick={() => props.toggleComplete(todo.id)} className={`btn btn-sm ${todo.isComplete ? 'btn-success' : 'btn-warning'}`}>{todo.isComplete ? 'Complete' : 'Running'}</button>
						<button onClick={()=>props.deleteTodo(todo.id)} className="btn btn-danger btn-sm mx-2">Delete</button>
						<button onClick={()=>props.getTodoInfo(todo.todoName,todo.todoDesc,todo.id)} className="btn btn-info btn-sm">Edit</button>
					</div>
				</li>
			))} 
		</ul>
	);
}
