import React from 'react' 
export default function Header(props) { 
  return (
    <>
      <h4 className="text-warning">Modern Todo</h4>
      <div className="d-flex my-3">
        <input onChange={props.setFilterSearch} value={props.filterSearch} type="text" placeholder="Search..." className="form-control form-control-sm" />
        <button onClick={props.toggleModal} className="btn btn-light btn-sm ml-2">Create<b className="text-light">_</b>Todo</button>
      </div>
      <div className="d-flex justify-content-between align-items-center">
        <div>
          <button onClick={props.setFilterOpen} className={`btn ${props.filterOpen === 'All' ? 'btn-primary rounded':'btn-secondary btn-sm rounded-0'}`}>All</button>
          <button onClick={props.setFilterOpen} className={`btn ${props.filterOpen === 'Completed' ? 'btn-primary rounded':'btn-secondary btn-sm rounded-0'}`}>Completed</button>
          <button onClick={props.setFilterOpen} className={`btn ${props.filterOpen === 'Running' ? 'btn-primary rounded':'btn-secondary btn-sm rounded-0'}`}>Running</button>
        </div>
        <div className="form-check text-white">
          <label className="form-check-label mx-2">
            <input onChange={props.changeView} type="radio" id="listview" name="tadoView" checked={props.viewChange === 'listview'} className="mr-1" />List View
          </label>
          <label className="form-check-label mx-2">
            <input onChange={props.changeView} type="radio" id="tableview" name="tadoView" checked={props.viewChange === 'tableview'} className="mr-1" />Table View
          </label>
        </div>
        <div>
          <button onClick={props.clearCompleted} className="btn btn-danger btn-sm">Clear Completed</button>
          <button onClick={props.clearSelected} className="btn btn-danger btn-sm mx-1">Clear Selected</button>
          <button className="btn btn-danger btn-sm">Reset Todo</button>
        </div>
      </div>
    </>
  )
}