import React, { Component } from 'react';

export default class CreateTodoModal extends Component {
	state = {
		todoName: '',
		todoDesc: '',
		errors: {}
	};

	componentDidMount() {
		const { todoName, todoDesc } = this.props;
		if (todoName) {
			this.setState({ todoName, todoDesc });
		}
	}

	changeHandler = (e) => this.setState({ [e.target.id]: e.target.value });

	submit = (e) => {
		const isValid = this.validate()
		e.preventDefault(); 
		if (isValid) {
			const {todoName,todoDesc} = this.state 
			this.props.submit({todoName,todoDesc});
			this.props.toggleModal();
		} 
	};

	validate = () => {
		const errors = {} 
		if (!this.state.todoName) {
			errors.todoName = 'Please Provide Todo Name'
		}
		this.setState({errors})
		return Object.keys(errors).length === 0 
	}

	render() {
		return (
			<div style={{ background: '#00000099' }} className="modal d-block">
				<div className="modal-dialog">
					<form onSubmit={this.submit} className="modal-content bg-dark">
						<div className="modal-header">
							<h6 className="modal-title text-warning">{this.props.btnValue} Todo</h6>
							<button
								type="button"
								onClick={this.props.toggleModal}
								className="btn btn-danger btn-sm px-4"
							>
								&times;
							</button>
						</div>
						<div className="modal-body text-justify">
							<label className="m-0 mt-2 text-info" htmlFor="todoName">
								Todo Name
							</label>
							<input
								onChange={this.changeHandler}
								value={this.state.todoName}
								type="text"
								placeholder="Todo name..."
								id="todoName"
								name="todoName"
								className={`form-control form-control-sm ${this.state.todoName ? 'is-valid':'is-invalid'}`}
							/>
							<small className="invalid-feedback">{this.state.errors.todoName}</small>
							<label className="m-0 mt-2 text-info" htmlFor="todoDesc">
								Todo Description (<small>Optional</small>)
							</label>
							<textarea
								onChange={this.changeHandler}
								value={this.state.todoDesc}
								placeholder="Todo description"
								id="todoDesc"
								name="todoDesc"
								className="form-control form-control-sm"
							></textarea>
						</div>
						<div className="modal-footer">
							<button type="submit" className="btn btn-success btn-sm">
								{this.props.btnValue} Todo
							</button>
							<button onClick={this.props.toggleModal} type="button" className="btn btn-danger btn-sm">
								Cencel
							</button>
						</div>
					</form>
				</div>
			</div>
		);
	}
}
