import React, { Component } from 'react';
import shortid from 'shortid';
import AllViewControler from './components/AllViewControler';
import CreateTodoModal from './components/CreateTodoModal';
import Header from './components/Header'; 

export default class index extends Component {
	state = {
		todos: [
			{
				todoName: 'Fruits',
				todoDesc: 'Apple, Orange, Mango, Banana, Coconut',
				id: shortid.generate(),
				date: new Date(),
				isSelect: false,
				isComplete: false,
			},
			{
				todoName: 'Vegetable',
				todoDesc: 'Bean, Cabbage, Cauliflower, Brinjal, Ginger, Chilly, Spice, Garlic, Onion',
				id: shortid.generate(),
				date: new Date(),
				isSelect: false,
				isComplete: false,
			},
		],
		viewChange: 'tableview',
		modalOpen: false,
		filterOpen: 'All',
		filterSearch: '',
		markAll:false
	};

	toggleMarkAll = () => {
		const { todos,markAll } = this.state 
		todos.forEach(todo => {
			todo.isSelect = !markAll
		})
		this.setState({todos,markAll:!markAll})
	}

	setFilterSearch = (e) => {
		this.setState({ filterSearch: e.target.value });
	}; 

	setFilterOpen = (e) => {
		this.setState({ filterOpen: e.target.innerText });
	};

	toggleModal = () => this.setState({ modalOpen: !this.state.modalOpen });

	changeView = (e) => this.setState({ viewChange: e.target.id });

	createTodo = (newTodo) => {
		newTodo.date = new Date();
		newTodo.id = shortid.generate();
		this.setState({ todos: this.state.todos.concat(newTodo) });
	};

	toggleComplete = id => {
		const { todos } = this.state 
		const todo = todos.find(todo => todo.id === id)
		todo.isComplete = !todo.isComplete
		this.setState({todos})
	}
	toggleSelect = id => {
		const { todos } = this.state 
		const todo = todos.find(todo=>todo.id === id) 
		todo.isSelect = !todo.isSelect
		this.setState({todos})
	}
	clearCompleted = () => {
		this.setState({todos:this.state.todos.filter(todo=>!todo.isComplete)})
	}
	clearSelected = () => {
		this.setState({markAll:false,todos:this.state.todos.filter(todo=>!todo.isSelect)})
	}

	deleteTodo = id => {
		let { todos } = this.state 
		todos = todos.filter(todo => todo.id !== id) 
		this.setState({todos})
	}

	updateTodo = (getUpdateInfo, id) => {
		const { todos } = this.state 
		const todo = todos.find(todo => todo.id === id) 
		todo.todoName = getUpdateInfo.todoName 
		todo.todoDesc = getUpdateInfo.todoDesc 
		this.setState({todos})
	}

	displayTodos = () => {
		const { filterOpen } = this.state;
		let todos = this.state.todos;
		todos = todos.filter(todo=>todo.todoName.toLowerCase().includes(this.state.filterSearch.toLowerCase()))
		if (filterOpen === 'Completed') {
			todos = todos.filter((todo) => todo.isComplete);
		} else if (filterOpen === 'Running') {
			todos = todos.filter((todo) => !todo.isComplete);
		}
		return <AllViewControler
			updateTodo={this.updateTodo}
			viewChange={this.state.viewChange}
			deleteTodo={this.deleteTodo}
			toggleMarkAll={this.toggleMarkAll}
			markAll={this.state.markAll}
			toggleSelect={this.toggleSelect}
			toggleComplete={this.toggleComplete}
			todos={todos}
		/>
	};

	render() {  
		return (
			<div style={{ background: '#000000', width: '100%', minHeight: '100vh', maxHeight: 'fit-content' }}>
				<div className="col-11 mx-auto p-3 bg-dark text-center">
					<Header
						toggleModal={this.toggleModal}
						changeView={this.changeView}
						viewChange={this.state.viewChange}
						filterOpen={this.state.filterOpen}
						setFilterOpen={this.setFilterOpen}
						filterSearch={this.state.filterSearch}
						setFilterSearch={this.setFilterSearch}
						clearSelected={this.clearSelected}
						clearCompleted={this.clearCompleted} 
					/>
					{this.displayTodos()}
				</div>

				{this.state.modalOpen && (
					<CreateTodoModal btnValue='Create' submit={this.createTodo} toggleModal={this.toggleModal} />
				)}
			</div>
		);
	}
}
